body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

  .carousel-indicators{
      /* margin-bottom: 0% */
  }

  .carousel-caption{
      font-weight: bold;
      margin-bottom: 15%;
     font-size: 60px;
     /* margin-bottom: 20%; */
  }

.carousel .item {
  height: 630px;
   -webkit-transition:1s;
   transition:1s;
}

.item img {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 300px;
}

@media screen and (max-width:870px){
    .item img{
       position: relative;
       top: 0;
       left: 0;
       min-height: 100%;
    }

    .carousel .item{
       height: 100%;
    }

.carousel-caption{
    font-size: 20px
}

}



